import { DeleteOutlined, TagsOutlined } from "@ant-design/icons";
import { Card, Button, Tag, Input, Image, Flex } from "antd";
import UICCheckNumber from "../../utils/UICCheckNumber";
import RollingStockData from "../../data/RollingStockData";
import { observer } from "mobx-react-lite";
import { useState } from "react";

function displayUICNumber(uicNumber: string) {
  if (UICCheckNumber(uicNumber)) {
    return uicNumber.replace(
      /(\d{2})(\d{2})(\d{4})(\d{3})(\d{1})/,
      "$1 $2 $3 $4-$5"
    );
  }
  return "Invalid UIC Number";
}

const RollingStock = observer(
  (props: {
    trainPosition: number;
    rollingStock: RollingStockData;
    remove: () => void;
  }) => {
    const { rollingStock } = props;

    const [hovered, setHovered] = useState(false);
    //   const handlerChangeUicNumber = (uicNumber: string) => {
    //     props.changeUicNumber(uicNumber.replace(/\D/g, ""));
    //   };

    const isUicNumberValid = UICCheckNumber(rollingStock.getUicNumber());
    return (
      <Flex vertical align="center">
        <Image width={200} src={rollingStock.getImageUrl()} />
        <Card
          title={
            <Tag
              icon={<TagsOutlined />}
              color={isUicNumberValid ? "blue" : "red"}
            >
              {displayUICNumber(rollingStock.getUicNumber())}
            </Tag>
          }
          style={{ width: 300 }}
          extra={
            hovered && (
              <Button onClick={props.remove} icon={<DeleteOutlined />} />
            )
          }
          onMouseEnter={() => setHovered(true)}
          onMouseLeave={() => setHovered(false)}
          size="small"
        >
          <Input
            type="text"
            value={rollingStock.getUicNumber()}
            onChange={(e) => rollingStock.setUicNumber(e.target.value)}
            status={isUicNumberValid ? "" : "error"}
          />
          <Input
            type="number"
            value={rollingStock.getMass()}
            onChange={(e) => {
              rollingStock.setMass(Number(e.target.value));
            }}
          />
        </Card>
      </Flex>
    );
  }
);

export default RollingStock;
